// src/app/questionarios/[id]/page.tsx
'use client';

import { useEffect, useState, Suspense, useMemo } from "react";
import api from "@/services/api";
import { useRouter, useParams } from "next/navigation";
import Link from "next/link";
import "../../globals.css";
import AdminAuthGuard from '@/components/auth/AdminAuthGuard';
import { PlusIcon, Trash2, BarChart, Edit, ArrowLeft, Eye } from "lucide-react";
import { PizzaChartComponent } from "@/components/charts/PizzaChart";
import { WordCloudComponent } from "@/components/charts/WordCloud";

// --- Interfaces (Mantidas e adaptadas) ---
interface Opcao {
    id?: number;
    texto: string;
    tempId?: string;
}
interface PerguntaAninhada {
    id?: number;
    enunciado: string;
    tipos: 'TEXTO' | 'MULTIPLA_ESCOLHA';
    opcoes: Opcao[];
    tempId?: string;
}
interface QuePerg {
    perguntaId?: number;
    questionarioId: number;
    pergunta: PerguntaAninhada;
    id?: number;
}
interface QuestionarioData {
    id: number;
    titulo: string;
}
interface RespostaDetalhada {
    id: number;
    resposta: string;
    pergunta: {
        id: number;
        enunciado: string;
        tipos: 'TEXTO' | 'MULTIPLA_ESCOLHA';
        opcoes: Opcao[];
    };
}
interface Respondente {
    id: number;
    status: string;
    isFinalizado: boolean;
    usuario?: { id: number; nome?: string | null; email: string };
    anonymousSessionId?: string | null;
    respostas: RespostaDetalhada[];
    created_at: string;
}
interface AvaliacaoComDetalhes {
    id: number;
    semestre: string;
    descricao: string; // Adicionado para identificar melhor a avaliação
    requerLoginCliente: boolean;
    usuarios: Respondente[];
    _count?: { usuarios: number };
    created_at: string;
}
// Tipo para os dados processados dos gráficos
type ProcessedChartData = {
    enunciado: string;
    tipos: 'TEXTO' | 'MULTIPLA_ESCOLHA';
    respostas: { [key: string]: number } | string[];
};


// --- Componente da Página ---
export default function EditQuestionarioPage() {
    return (
        <Suspense fallback={<div className="page-container center-content"><p>Carregando...</p></div>}>
            <AdminAuthGuard>
                <EditQuestionarioFormContent />
            </AdminAuthGuard>
        </Suspense>
    );
}

// --- Componente de Conteúdo Principal ---
function EditQuestionarioFormContent() {
    const router = useRouter();
    const params = useParams();
    const questionarioId = Number(params.id);

    // Estados para edição
    const [titulo, setTitulo] = useState("");
    const [quePergs, setQuePergs] = useState<QuePerg[]>([]);
    const [isLoading, setIsLoading] = useState(true);
    const [error, setError] = useState<string | null>(null);

    // Estados para visualização
    const [viewMode, setViewMode] = useState<'editar' | 'respostas'>('editar');
    const [respostasSubView, setRespostasSubView] = useState<'dashboard' | 'individuais'>('dashboard');
    const [avaliacoesComRespostas, setAvaliacoesComRespostas] = useState<AvaliacaoComDetalhes[]>([]);
    const [isLoadingRespostas, setIsLoadingRespostas] = useState(false);
    const [filtroAvaliacaoId, setFiltroAvaliacaoId] = useState<string>('all'); // 'all' ou o ID da avaliação

    // --- Lógica de Carregamento e Manipulação de Dados ---
    useEffect(() => {
        // ... (Sua lógica para carregar o questionário para edição - sem alterações)
        if (!questionarioId) {
            setError("ID do Questionário não encontrado.");
            setIsLoading(false);
            return;
        }
        setIsLoading(true);
        const loadData = async () => {
            try {
                const respQuestionario = await api.get<QuestionarioData>(`/questionarios/${questionarioId}`);
                setTitulo(respQuestionario.data.titulo);
                const respQuePerg = await api.get<QuePerg[]>(`/quePerg?questionarioId=${questionarioId}`);
                const sanitizedQuePergs = respQuePerg.data.map(qp => ({...qp, pergunta: {...qp.pergunta, opcoes: (qp.pergunta.opcoes || []).map(opt => ({...opt }))}}));
                setQuePergs(sanitizedQuePergs);
            } catch (err: any) {
                setError("Falha ao carregar dados para edição.");
            } finally {
                setIsLoading(false);
            }
        };
        loadData();
    }, [questionarioId]);

    useEffect(() => {
        // Busca os dados das respostas quando a aba é aberta
        if (viewMode === 'respostas' && questionarioId) {
            setIsLoadingRespostas(true);
            setError(null);
            api.get<AvaliacaoComDetalhes[]>(`/questionarios/${questionarioId}/avaliacoes-com-respostas`)
                .then(response => {
                    setAvaliacoesComRespostas(response.data.sort((a,b) => new Date(b.created_at).getTime() - new Date(a.created_at).getTime()));
                })
                .catch(err => {
                    setError("Erro ao buscar dados das respostas.");
                })
                .finally(() => {
                    setIsLoadingRespostas(false);
                });
        }
    }, [viewMode, questionarioId]);

    // --- Lógica de processamento dos dados para o Dashboard ---
    const dadosProcessadosParaDashboard = useMemo((): [number, ProcessedChartData][] => {
        if (avaliacoesComRespostas.length === 0) return [];
    
        const avaliacoesFiltradas = filtroAvaliacaoId === 'all'
            ? avaliacoesComRespostas
            : avaliacoesComRespostas.filter(av => av.id === Number(filtroAvaliacaoId));

        const dadosAgregados = new Map<number, ProcessedChartData>();

        for (const avaliacao of avaliacoesFiltradas) {
            for (const respondente of avaliacao.usuarios) {
                for (const resposta of respondente.respostas) {
                    const { pergunta } = resposta;
                    if (!pergunta) continue;

                    if (!dadosAgregados.has(pergunta.id)) {
                        dadosAgregados.set(pergunta.id, {
                            enunciado: pergunta.enunciado,
                            tipos: pergunta.tipos,
                            respostas: pergunta.tipos === 'MULTIPLA_ESCOLHA' ? {} : [],
                        });
                    }

                    const dadosPergunta = dadosAgregados.get(pergunta.id)!;
                    if (dadosPergunta.tipos === 'MULTIPLA_ESCOLHA') {
                        const resps = dadosPergunta.respostas as { [key: string]: number };
                        resps[resposta.resposta] = (resps[resposta.resposta] || 0) + 1;
                    } else {
                        (dadosPergunta.respostas as string[]).push(resposta.resposta);
                    }
                }
            }
        }
        return Array.from(dadosAgregados.entries());
    }, [avaliacoesComRespostas, filtroAvaliacaoId]);

    const kpis = useMemo(() => {
        const avaliacoesFiltradas = filtroAvaliacaoId === 'all'
            ? avaliacoesComRespostas
            : avaliacoesComRespostas.filter(av => av.id === Number(filtroAvaliacaoId));
        
        let totalRespondentes = 0;
        let finalizados = 0;

        avaliacoesFiltradas.forEach(av => {
            totalRespondentes += av.usuarios.length;
            av.usuarios.forEach(u => {
                if (u.isFinalizado) finalizados++;
            });
        });

        const taxaConclusao = totalRespondentes > 0 ? (finalizados / totalRespondentes * 100).toFixed(1) : '0.0';

        return { totalRespondentes, finalizados, taxaConclusao };
    }, [avaliacoesComRespostas, filtroAvaliacaoId]);


    // --- Funções de manipulação do formulário (sem alterações) ---
    const handlePerguntaChange = (qIndex: number, novoEnunciado: string) => setQuePergs(prev => prev.map((qp, i) => i === qIndex ? {...qp, pergunta: {...qp.pergunta, enunciado: novoEnunciado}} : qp));
    const handleTipoChange = (qIndex: number, novoTipo: 'TEXTO' | 'MULTIPLA_ESCOLHA') => setQuePergs(prev => prev.map((qp, i) => i === qIndex ? {...qp, pergunta: {...qp.pergunta, tipos: novoTipo, opcoes: novoTipo === 'TEXTO' ? [] : qp.pergunta.opcoes.length > 0 ? qp.pergunta.opcoes : [{texto: '', tempId: `opt-0`}]}} : qp));
    const handleOptionChange = (qIndex: number, oIndex: number, novoTexto: string) => setQuePergs(prev => prev.map((qp, i) => i === qIndex ? {...qp, pergunta: {...qp.pergunta, opcoes: qp.pergunta.opcoes.map((o, oi) => oi === oIndex ? {...o, texto: novoTexto} : o)}} : qp));
    const addOptionToList = (qIndex: number) => setQuePergs(prev => prev.map((qp, i) => i === qIndex ? {...qp, pergunta: {...qp.pergunta, opcoes: [...qp.pergunta.opcoes, {texto: '', tempId: `opt-${qp.pergunta.opcoes.length}`}]}} : qp));
    const removeOption = (qIndex: number, oIndex: number) => setQuePergs(prev => prev.map((qp, i) => i === qIndex ? {...qp, pergunta: {...qp.pergunta, opcoes: qp.pergunta.opcoes.filter((_, oi) => oi !== oIndex)}} : qp));
    const handleAddNewPergunta = () => setQuePergs(prev => [...prev, { questionarioId, pergunta: { enunciado: '', tipos: 'TEXTO', opcoes: [], tempId: `new-${Date.now()}`}}]);
    const handleSaveChanges = async (e: React.FormEvent) => { /* ... sua lógica de salvar ... */
        e.preventDefault();
        // Sua lógica para salvar as perguntas e o título continua aqui
        alert("Lógica de salvar executada!");
     };

    // --- Renderização do Loading e Erro Inicial ---
    if (isLoading) return <div className="page-container center-content"><p>Carregando dados do questionário...</p></div>;
    if (error && !titulo) return <div className="page-container center-content"><p className="error-message">{error}</p></div>;
    
    // --- Renderização Principal ---
    return (
        <div className="page-container">
            {/* Cabeçalho Fixo com Título e Navegação */}
            <div className="flex items-center justify-between mb-4 pb-4 border-b border-border">
                <div className="flex items-center gap-3">
                    <button onClick={() => router.push('/questionarios')} className="btn btn-icon">
                        <ArrowLeft size={20} />
                    </button>
                    <h2 className="text-2xl font-bold text-foreground">{titulo}</h2>
                </div>
                <div className="flex items-center gap-2">
                    <button onClick={() => setViewMode('editar')} className={`btn ${viewMode === 'editar' ? 'btn-primary' : 'btn-outline'}`}><Edit size={16} className="mr-2"/>Configurar</button>
                    <button onClick={() => setViewMode('respostas')} className={`btn ${viewMode === 'respostas' ? 'btn-primary' : 'btn-outline'}`}><BarChart size={16} className="mr-2"/>Visualizar Respostas</button>
                </div>
            </div>

            {/* View de Edição */}
            {viewMode === 'editar' && (
                <form onSubmit={handleSaveChanges} className="editor-form-card">
                    {/* ... (Todo o seu JSX para o formulário de edição vai aqui, sem alterações) ... */}
                    <p className="text-center p-8 bg-gray-100 rounded-md">Seu formulário de edição de perguntas e título aparece aqui.</p>
                </form>
            )}

            {/* View de Respostas com Dashboards */}
            {viewMode === 'respostas' && (
                <div>
                    {isLoadingRespostas && <p className="text-center py-8">Carregando dashboards...</p>}
                    {!isLoadingRespostas && error && <p className="text-center py-8 error-message">{error}</p>}
                    {!isLoadingRespostas && avaliacoesComRespostas.length === 0 && !error &&
                        <div className="text-center py-10 px-4 bg-element-bg rounded-lg shadow-md mt-6">
                            <BarChart className="mx-auto h-12 w-12 text-text-muted" />
                            <h3 className="mt-2 text-sm font-medium text-text-base">Sem Dados de Respostas</h3>
                            <p className="mt-1 text-sm text-text-muted">Nenhuma resposta foi submetida para este questionário ainda.</p>
                        </div>
                    }

                    {!isLoadingRespostas && avaliacoesComRespostas.length > 0 && (
                        <>
                            {/* Header do Dashboard com Filtros */}
                            <div className="flex flex-col sm:flex-row items-center justify-between mb-6 p-4 bg-element-bg rounded-lg border border-border">
                                <div className="flex items-center gap-4">
                                    <label htmlFor="filtro-avaliacao" className="font-medium text-text-base">Analisar Avaliação:</label>
                                    <select
                                        id="filtro-avaliacao"
                                        value={filtroAvaliacaoId}
                                        onChange={(e) => setFiltroAvaliacaoId(e.target.value)}
                                        className="select-field" // Use sua classe de select global
                                    >
                                        <option value="all">Todas as Avaliações</option>
                                        {avaliacoesComRespostas.map(av => (
                                            <option key={av.id} value={av.id}>
                                                {av.semestre} - {av.descricao || `(ID: ${av.id})`}
                                            </option>
                                        ))}
                                    </select>
                                </div>
                                <button onClick={() => setRespostasSubView(respostasSubView === 'dashboard' ? 'individuais' : 'dashboard')} className="btn btn-secondary mt-3 sm:mt-0">
                                    <Eye size={16} className="mr-2"/>
                                    {respostasSubView === 'dashboard' ? 'Ver Respostas Individuais' : 'Ver Dashboard Agregado'}
                                </button>
                            </div>

                            {/* Sub-view do Dashboard Agregado */}
                            {respostasSubView === 'dashboard' && (
                                <>
                                    {/* KPIs */}
                                    <div className="grid grid-cols-1 sm:grid-cols-3 gap-4 mb-6">
                                        <div className="kpi-card"><span className="kpi-value">{kpis.totalRespondentes}</span><span className="kpi-label">Total de Respostas</span></div>
                                        <div className="kpi-card"><span className="kpi-value">{kpis.finalizados}</span><span className="kpi-label">Respostas Finalizadas</span></div>
                                        <div className="kpi-card"><span className="kpi-value">{kpis.taxaConclusao}%</span><span className="kpi-label">Taxa de Conclusão</span></div>
                                    </div>
                                    {/* Gráficos */}
                                    <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
                                        {dadosProcessadosParaDashboard.map(([perguntaId, dados]) => (
                                            <div key={perguntaId} className="chart-card">
                                                <h4 className="chart-title">{dados.enunciado}</h4>
                                                {dados.tipos === 'MULTIPLA_ESCOLHA' ? (
                                                    <PizzaChartComponent data={dados.respostas as { [key: string]: number }} />
                                                ) : (
                                                    <WordCloudComponent textData={dados.respostas as string[]} />
                                                )}
                                            </div>
                                        ))}
                                    </div>
                                </>
                            )}

                            {/* Sub-view das Respostas Individuais (Sua lógica original) */}
                            {respostasSubView === 'individuais' && (
                                <div className="space-y-4">
                                     <h3 className="text-xl font-semibold">Respostas Individuais</h3>
                                     {/* A sua lógica de listar por semestre e depois por respondente vai aqui */}
                                     <p className="p-4 bg-gray-100 rounded-md text-center">A lista detalhada de respostas individuais apareceria aqui.</p>
                                </div>
                            )}
                        </>
                    )}
                </div>
            )}
        </div>
    );
}
